body {
  background: url(./images/bg.jpg) center no-repeat;
  background-size: cover;
  width: 100vw;
  min-height: 100vh;
}

.weatherApp {
  display: flex;
  flex-direction: column;
  padding: 6% 12%;
  color: #97ceff;
  font-family: monospace;
}

.city {
  font-size: -webkit-xxx-large;
}

.weatherInfo {
  display: flex;
  flex-direction: column;
}

.weatherInfo h6 {
  margin-block-end: 0;
}

.weatherInfo p {
  margin-block-start: 0.5em;
  font-size: larger;
}

.section {
  display: flex;
  justify-content: space-around;
  font-size: xx-large;
}

.temp {
  display: flex;
  flex-direction: row;
}

.feelsLike {
  display: flex;
  flex-direction: row;
}

.humidity {
  display: flex;
  flex-direction: row;
}

.windSpeed {
  display: flex;
  flex-direction: row;
}

.visibility {
  display: flex;
  flex-direction: row;
}

.cloudiness {
  display: flex;
  flex-direction: row;
}

.form__group {
  position: relative;
  padding: 15px 0 0;
  margin-top: 10px;
  width: 50%;
}
.form__field {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 2px solid #9b9b9b;
  outline: 0;
  font-size: 1.3rem;
  color: #fff;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}
.form__field::placeholder {
  color: transparent;
}
.form__field:placeholder-shown ~ .form__label {
  font-size: 1.3rem;
  cursor: text;
  top: 20px;
}
.form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: #9b9b9b;
}
.form__field:focus {
  padding-bottom: 6px;
  font-weight: 700;
  border-width: 3px;
  border-image: linear-gradient(to right, #11998e, #38ef7d);
  border-image-slice: 1;
}
.form__field:focus ~ .form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: #11998e;
  font-weight: 700;
}
/* reset input */
.form__field:required, .form__field:invalid {
  box-shadow: none;
}
